import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'

Vue.use(VueRouter);

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home
  },
  {
    name: 'Service',
    path: '/service',
    component: () => import(/* webpackChunkName: "Service" */ '../views/Service.vue')
  },
  {
    name: 'Education',
    path: '/education',
    redirect: '/education/vocational-education',
    component: () => import(/* webpackChunkName: "Education" */ '../views/Education.vue'),
    children: [
      {
        path: 'vocational-education',
        component: () => import(/* webpackChunkName: "Service" */ '@education/vocational.vue')
      },
      {
        path: 'education-training',
        component: () => import(/* webpackChunkName: "Service" */ '@education/training.vue')
      }
    ]
  },
  {
    name: 'Cooperate',
    path: '/cooperate',
    redirect: '/cooperate/cooperate-colleges',
    component: () => import(/* webpackChunkName: "Cooperate" */ '../views/Cooperate.vue'),
    children: [
      {
        path: 'cooperate-colleges',
        component: () => import(/* webpackChunkName: "Colleges" */ '@cooperate/colleges.vue')
      },
      {
        path: 'school-enterprise-cooperate',
        component: () => import(/* webpackChunkName: "SchoolEnterprise" */ '@cooperate/schoolEnterprise.vue')
      }
    ]
  },
  {
    name: 'News',
    path: '/news',
    component: () => import(/* webpackChunkName: "News" */ '../views/News.vue'),
  },
  {
    name: 'About',
    path: '/about',
    component: () => import(/* webpackChunkName: "About" */ '../views/About.vue'),
  },
  {
    name: 'Details',
    path: '/details/:id',
    component: () => import(/* webpackChunkName: "About" */ '../views/Details.vue'),
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes,
  scrollBehavior(to, from, savedPosition) { // 刷新或切换路由滚动条回到顶部
    return { x: 0, y: 0 }
  }
})



export default router
