<template>
  <div class="seat">
    <div class="ls-scroll-container" ref="scrollMenu">
      <div class="ls-scroll-menu" ref="clickMenu">
        <slot></slot>
      </div>
      <div class="ls-line" ref="line"></div>
    </div>
  </div>
</template>

<script>
import { getOffset, debounce } from "@utils";

let isFirstClick = true; // 是否为首次点击
let pervTargetT = null; // 存储上一个目标元素距离浏览器的Y轴偏移量
let fristLoadEl = null; // 存储首次只是滚动的内容元素

export default {
  name: "ls-scroll-menu",
  props: {
    isFixed: {
      type: Boolean,
      default: true,
    },
    top: {
      type: Number,
      default: 72,
    },
    container: {
      type: Function,
      default: () => window,
    },
    offsetTop: {
      type: Number,
      default: 72,
    },
    targetOffsetTop: {
      type: Number,
      default: 0.7,
    },
  },
  data() {
    return {
      debounce: debounce,
    };
  },
  computed: {
    target() {
      return this.container();
    },
    lineElm() {
      return this.$refs["line"];
    },
    scrollMenu() {
      return this.$refs["scrollMenu"];
    },
    selfT() {
      return getOffset(this.scrollMenu).t;
    },
    targetList() {
      let targets = [];
      this.$children.forEach((child) => {
        targets.push(document.querySelector(child.id));
      });
      return targets;
    },
  },
  mounted() {
    // 滚动固定
    if (this.isFixed) {
      this.target.addEventListener("scroll", this.fixedContainer, {
        passive: true,
      });
    }
    // 点击滚动
    this.$refs.clickMenu.addEventListener(
      "click",
      debounce(this, this.smoothScroll, 1000, true)
    );
    // 滚动跟随焦点
    window.addEventListener("load", this.followScroll);
  },
  beforeDestroy() {
    if (this.isFixed) {
      this.target.removeEventListener("scroll", this.fixedContainer, {
        passive: true,
      });
    }
    window.removeEventListener("load", this.followScroll);
  },
  methods: {
    fixedContainer() {
      let targetT = document.documentElement.scrollTop;
      if (targetT > this.selfT - this.offsetTop) {
        this.scrollMenu.style.position = "fixed";
        this.scrollMenu.style.top = `${this.top}px`;
      } else {
        this.scrollMenu.style.position = "relative";
        this.scrollMenu.style.top = 0;
      }
    },
    smoothScroll(e) {
      let el = e.target;
      if (el.tagName === "A") {
        let isStop = false;
        let id = el.getAttribute("id");
        let doc = document.documentElement;
        let selfH = this.scrollMenu.scrollHeight;
        let target = document.querySelector(id);
        let targetT = getOffset(target).t;

        // 判断是否为首次点击
        isFirstClick
          ? fristLoadEl
            ? (pervTargetT = getOffset(fristLoadEl).t)
            : null
          : null;

        // 向上滚动
        let scrollUp = (winT, limitT) => {
          if (winT <= limitT) {
            clreanAnimation();
          } else {
            doc.scrollTop = winT - (winT - limitT) / 10;
            window.requestAnimationFrame(animationFn);
          }
        };
        // 向下滚动
        let scrollDown = (winT, limitT) => {
          if (winT >= limitT) {
            clreanAnimation();
          } else {
            // 当前位置 + (目标位置 - 当前位置) / 10 由快到慢的动画
            doc.scrollTop = winT + (targetT - winT) / 10;
            window.requestAnimationFrame(animationFn);
          }
        };
        // 清除动画
        let clreanAnimation = () => {
          if (!isStop) {
            pervTargetT = targetT;
            isStop = true;
          }
          window.cancelAnimationFrame(animationFn);
        };
        // 动画函数
        let animationFn = () => {
          let winT = doc.scrollTop;
          let limitT = targetT - (selfH + this.offsetTop);
          if (targetT < pervTargetT) {
            // targetT 小于 pervTargetT 那么就向上滚动
            scrollUp(winT, limitT);
          } else {
            scrollDown(winT, limitT);
          }
        };
        animationFn();

        // 设置阈值为false
        isFirstClick ? (isFirstClick = false) : null;
      }
    },
    followScroll() {
      // 监听元素是否出现在页面
      let io = new IntersectionObserver(
        (entries) => {
          for (let i = 0; i < entries.length; i++) {
            let entry = entries[i];
            if (entry.isIntersecting) {
              // 显示在页面上
              let key = this.targetList.indexOf(entry.target);
              let offsetLeft = this.$children[key].$el.offsetLeft;
              let curX =
                window.innerWidth > 375 ? offsetLeft - 15 : offsetLeft - 6;
              this.lineElm.style.transform = `translateX(${curX}px)`;
              if (isFirstClick) {
                fristLoadEl = entry.target;
              }
            }
          }
        },
        {
          threshold: this.targetOffsetTop,
        }
      );
      this.targetList.forEach((target) => io.observe(target));
    },
  },
};
</script>

<style lang="less">
.seat {
  /* 占位盒子 */
  width: 100%;
  height: 133px;
}

.ls-scroll-container {
  width: 100%;
  left: 0;
  padding: 50px 30px;
  background-color: #ffffff;
  z-index: 10000;
}
.ls-scroll-menu {
  display: flex;
}
.ls-line {
  margin-top: 8px;
  width: 80px;
  border: 2px solid #3553e8;
  transform: translateX(15px);
  transition: transform 0.5s cubic-bezier(0.3, 0.21, 0.9, 1.42);
}

@media screen and (max-width: 375px) {
  .seat {
    height: 93px;
  }
  .ls-scroll-menu {
    justify-content: center;
  }
  .ls-scroll-container {
    padding: 30px 0;
  }
  .ls-line {
    width: 70px;
    transform: translateX(6px);
  }
}
</style>