<template>
  <div class="ls-card">
    <div class="ls-card-img-comtainer" :style="reverseStyle">
      <slot name="img"></slot>
      <div class="ls-card-sub">
        <div class="ls-card-img-triangle"></div>
        <div class="ls-card-move-title">{{ title }}</div>
      </div>
    </div>
    <slot></slot>
  </div>
</template>

<script>
export default {
  name: "ls-card",
  props: {
    title: String,
    reverse: Boolean,
  },
  data() {
    return {
      reverseStyle: {
        order: this.reverse ? 1 : 0,
      },
    };
  },
  mounted() {
    this.resize();
    window.addEventListener("resize", this.resize);
  },
  beforeDestroy() {
    window.removeEventListener("resize", this.resize);
  },
  methods: {
    resize() {
      // 当视口改变，监听是否小于800，如果小于那就不能reverse
      if (window.innerWidth <= 800) {
        this.reverseStyle.order = 0;
      } else {
        this.reverseStyle.order = this.reverse ? 1 : 0;
      }
    },
  },
};
</script>

<style lang="less" scoped>
.ls-card {
  display: flex;
  margin: 70px 20px 0px 20px;
  justify-content: center;
  align-items: center;
  background: #ffffff;
}
.ls-card-img-comtainer {
  position: relative;
  max-width: 500px;
  min-width: 357px;
  max-height: 380px;
  img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
}
.ls-card-img-triangle {
  border-bottom: 249px solid #3553e8;
  border-left: 335px solid transparent;
  opacity: 0.5;
}

.ls-card-sub {
  display: none;
}

@media screen and (max-width: 800px) {
  .ls-card {
    margin: 30px 5px 0 5px;
    width: 335px;
    height: 459px;
    flex-direction: column;
    box-shadow: 0px 3px 6px rgba(0, 0, 0, 0.16);
  }
  .ls-card-img-comtainer {
    max-width: 335px;
    min-width: auto;
    max-height: 249px;
    width: 335px;
    height: 249px;
  }
  .ls-card-sub {
    display: block;
  }
  .ls-card-img-triangle {
    position: absolute;
    top: 0;
    left: 0;
  }
  .ls-card-move-title {
    position: absolute;
    right: 10px;
    bottom: 10px;
    color: #ffffff;
    font-size: 16px;
  }
}
</style>