<template>
  <div class="ls-tabs-item" @click="moveLine">
    <router-link tag="span" :to="path">
      <slot> </slot>
    </router-link>
  </div>
</template>

<script>
export default {
  name: "ls-tabs-item",
  props: {
    path: String,
  },
  methods: {
    moveLine(e) {
      if (e.target.tagName === "SPAN") {
        let x = e.target.offsetLeft;
        let line = this.$parent.$refs.line;
        line.style.transform = `translateX(${x - 12}px)`;
      }
    },
  },
};
</script>

<style lang="less" scoped>
.ls-tabs-item {
  margin-right: 60px;
  padding: 8px 12px;
  font-size: 14px;
  span {
    cursor: pointer;
  }
}
</style>